import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
import axios from '@/utils/uniAxios.js'
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			orderList: []

		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.loadData()
	},

	methods: {
		buy() {
			uni.navigateTo({
				url: '../index/index'
			})
		},
		//获取订单列表
		loadData() {
			axios.post('/returnApply/list').then(res => {
				console.log(res)
				if (res.data.code == 200) {
					this.orderList = res.data.data;
					this.orderList.push({
						companyAddressId: 1,
						createTime: "2019-12-19T14:34:39.000+0000",
						description: '',
						handleMan: '',
						handleNote: '',
						handleTime: "2019-12-19T14:34:39.000+0000",
						id: 145,
						memberId: 76,
						memberUsername: '',
						orderId: 145,
						orderSn: "201912190102000002",
						productAttr: '',
						productBrand: "明知缘",
						productCount: 1,
						productId: 45,
						productName: "正宗农家散养谷饲土鸡蛋20枚",
						productPic: 'http://maitianquan-zjk.oss-cn-zhangjiakou.aliyuncs.com/cropcircle/image/20191204/好货推荐-1.png',
						productPrice: 25.99,
						productRealPrice: 25.99,
						proofPics: '',
						reason: '不想要了',
						receiveMan: "小鲁",
						receiveNote: '备注',
						receiveTime: "2019-12-19T14:34:39.000+0000",
						returnAmount: 25.99,
						returnName: "小鲁",
						returnPhone: 13667138671,
						status: 0
						// status: 0申请状态：0->待处理；1->退货中；2->已完成；3->已拒绝
					})
					console.log(this.orderList)
				}
			})
		},
		returnState(n) {
			if (n == 0) {
				return '待处理'
			} else if (n == 1) {
				return '退货中'
			} else if (n == 2) {
				return '已完成'
			} else {
				return '已拒绝'
			}
		},
		// 评价
		evaluate(item) {
			console.log(item)
		},
		// 查看详情
		lookDetail(item) {
			let id = item.id;
			uni.navigateTo({
				url:'closeOrder?id='+id
			})
			// uni.navigateTo({
			// 	url: 'refund?id='+id
			// })
			console.log(item)
		},
		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 5:
					stateTip = '';
					break;
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
}
